  -ms-flex-pack: center;
      justify-content: center;
}
ul {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #ddd;
}
ul li {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  /*display: flex;*/
}
li a {
